.responsive-svg-container {
  width: 100%;
  max-width: 1200px;
  position: relative;
}

.responsive-svg-container.small {
  max-width: 300px;
  margin: 0 auto;
}

.tick text {
  font-family: Open Sans, sans-serif;
  font-size: 1.5rem;
}

#scatterplot text {
  fill: #192e4d;
}

#scatterplot line, #scatterplot path {
  stroke-width: 2px;
}

#filters {
  flex-wrap: wrap;
  display: flex;
}

.filter {
  cursor: pointer;
  background-color: #192e4d99;
  border: none;
  border-radius: 4px;
  margin-bottom: 15px;
  margin-right: 15px;
  padding: 10px 20px 10px 15px;
  font-family: inherit;
  transition: background-color .2s;
}

.filter-icon, .filter-text {
  vertical-align: middle;
  display: inline-block;
}

.filter-icon {
  background-repeat: no-repeat;
  background-size: contain;
  width: 30px;
  height: 30px;
  margin-right: 10px;
}

.filter-text {
  color: #f5f9ff;
  font-size: 1.8rem;
}

.filter:hover, .filter.active {
  background-color: #192e4d;
}

.filter.active {
  cursor: default;
}

.legend-sections {
  display: flex;
}

.legend-section {
  margin-right: 100px;
}

.legend-conservation-status ul {
  margin: 0;
  padding-left: 5px;
  list-style: none;
}

.legend-conservation-status li {
  align-items: center;
  margin: 10px 0;
  display: flex;
}

.legend-conservation-status svg {
  margin-right: 10px;
}

